.profile-img {
  width: 150px;
  position: absolute;
}

@media only screen and (max-width: 768px) {
  .profile-img {
    position: relative;
    top: 0;
    left: 0;
    right: 0;
    width: 180px;
    height: 180px;
    margin-bottom: 20px;
  }
  .profile-img-container {
    text-align: center;
  }
  .project-stack {
    text-align: left;
    margin-left: -15px;
  }
  .project-row {
    margin-right: 0px;
  }
  .emp-hist-img {
    margin-top: 10px;
  }
}

.social-icon {
  width: 25px;
}

.header-section {
  margin-left: -15px;
  margin-right: -12px;
}

.header-banner {
  background-color: #084e69;
}

.theme-switch {
  text-align: right;
}

.theme-switch .btn-clear {
  padding: 0;
}

.theme-switch .btn-clear:focus,
.theme-switch .btn-clear:active {
  outline: none;
  box-shadow: none;
}

.theme-switch .mode-text {
  font-size: 12px;
  font-style: italic;
  margin-right: 5px;
  color: var(--light-gray-alt);
}

.theme-dark {
  background-color: var(--drk-theme-bg-color) !important;
}
.theme-dark .header-section .header-banner {
  background-color: var(--drk-theme-gray);
}

.theme-dark .mode-text {
  color: var(--white);
}
